"use client";

import { useState, useEffect } from "react";
import { BACKEND_URL } from "@/lib/config";
import { ShipmentFromApi, ShipmentsResponse } from "@/lib/types/shipment.types";
import { useI18n } from "@/i18n/i18nProvider";

const PAGE_SIZE = 30;

type HistoryShipmentState = {
  id: string;
  trackingNumber: string;
  companyName: string;
} | null;

export function useShipments() {
  const { t } = useI18n();

  const [companyFilter, setCompanyFilter] = useState("");
  const [selectedDate, setSelectedDate] = useState(""); // YYYY-MM-DD
  const [sortOrder, setSortOrder] = useState<"newest" | "oldest">("newest");

  const [shipments, setShipments] = useState<ShipmentFromApi[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [total, setTotal] = useState(0);

  const [historyOpen, setHistoryOpen] = useState(false);
  const [historyShipment, setHistoryShipment] =
    useState<HistoryShipmentState>(null);

  const totalPages = Math.max(Math.ceil(total / PAGE_SIZE), 1);

  async function loadShipments(page: number) {
    try {
      setLoading(true);
      setError(null);

      const url = new URL(`${BACKEND_URL}/api/shipments`);
      url.searchParams.set("page", String(page));
      url.searchParams.set("pageSize", String(PAGE_SIZE));

      if (companyFilter.trim()) {
        url.searchParams.set("companyName", companyFilter.trim());
      }

      if (selectedDate) {
        url.searchParams.set("date", selectedDate);
      }

      url.searchParams.set("sort", sortOrder);

      const res = await fetch(url.toString());
      if (!res.ok) {
        throw new Error("Failed to fetch shipments");
      }

      const data = (await res.json()) as ShipmentsResponse;
      setShipments(data.items);
      setTotal(data.total);
      setCurrentPage(data.page);
    } catch (err) {
      console.error(err);
      setError(t("dashboard.loadError") ?? "Nepodařilo se načíst zásilky.");
    } finally {
      setLoading(false);
    }
  }

  // inicialní načtení
  useEffect(() => {
    loadShipments(1);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // kdykoli se změní filtry, načteme znovu stránku 1
  useEffect(() => {
    loadShipments(1);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [companyFilter, selectedDate, sortOrder]);

  // callback pro otevření historie
  const openHistoryForShipment = (s: ShipmentFromApi) => {
    setHistoryShipment({
      id: s.id,
      trackingNumber: s.trackingNumber,
      companyName: s.company.name,
    });
    setHistoryOpen(true);
  };

  return {
    // filtry
    companyFilter,
    setCompanyFilter,
    selectedDate,
    setSelectedDate,
    sortOrder,
    setSortOrder,

    // data
    shipments,
    loading,
    error,
    currentPage,
    total,
    totalPages,

    // historie dialog
    historyOpen,
    setHistoryOpen,
    historyShipment,
    setHistoryShipment,
    openHistoryForShipment,

    // načítání
    loadShipments,
  };
}
