// src/hooks/useShipmentHistory.ts
"use client";

import { useEffect, useState, useCallback } from "react";
import { fetchShipmentHistory, ShipmentHistoryItem } from "@/app/api/shipments";

type UseShipmentHistoryState = {
  items: ShipmentHistoryItem[];
  loading: boolean;
  error: Error | null;
  reload: () => void;
};

export function useShipmentHistory(
  shipmentId: string | null,
  enabled: boolean
): UseShipmentHistoryState {
  const [items, setItems] = useState<ShipmentHistoryItem[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<Error | null>(null);

  const load = useCallback(async () => {
    if (!shipmentId || !enabled) return;

    const controller = new AbortController();
    const { signal } = controller;

    setLoading(true);
    setError(null);

    try {
      const result = await fetchShipmentHistory(shipmentId, signal);
      setItems(result.items);
    } catch (err) {
      if ((err as Error).name === "AbortError") {
        return;
      }
      console.error(err);
      setError(err as Error);
      setItems([]);
    } finally {
      setLoading(false);
    }

    return () => controller.abort();
  }, [shipmentId, enabled]);

  useEffect(() => {
    let controller: AbortController | null = null;

    if (shipmentId && enabled) {
      controller = new AbortController();
      const { signal } = controller;

      setLoading(true);
      setError(null);

      fetchShipmentHistory(shipmentId, signal)
        .then((result) => {
          setItems(result.items);
        })
        .catch((err) => {
          if ((err as Error).name === "AbortError") return;
          console.error(err);
          setError(err as Error);
          setItems([]);
        })
        .finally(() => {
          setLoading(false);
        });
    } else {
      setItems([]);
      setError(null);
      setLoading(false);
    }

    return () => {
      controller?.abort();
    };
  }, [shipmentId, enabled]);

  return {
    items,
    loading,
    error,
    reload: load,
  };
}
