"use client";

import { useI18n } from "@/i18n/i18nProvider";
import { Clock } from "lucide-react";

type ShipmentCardProps = {
  trackingNumber: string;
  companyName: string;
  provider: string;
  price: number;
  currency?: string;
  createdAt: string;
  originCountry: string;
  destinationCountry: string;
  mode: "EXPORT" | "IMPORT" | string;
  hasHistory: boolean;
  onShowHistory: () => void;
  invoiceCount?: number;
};

export function ShipmentCard({
  trackingNumber,
  companyName,
  provider,
  price,
  currency = "CZK",
  createdAt,
  originCountry,
  destinationCountry,
  mode,
  hasHistory,
  onShowHistory,
  invoiceCount,
}: ShipmentCardProps) {
  const { locale, t } = useI18n();

  const formattedDate = new Date(createdAt).toLocaleDateString(
    locale === "cs" ? "cs-CZ" : "en-US"
  );

  const formattedPrice = new Intl.NumberFormat(
    locale === "cs" ? "cs-CZ" : "en-US",
    { style: "currency", currency }
  ).format(price);

  const isExport = mode === "EXPORT";
  const modeLabel =
    mode === "EXPORT"
      ? t("dashboard.export")
      : mode === "IMPORT"
      ? t("dashboard.import")
      : mode;

  return (
    <div
      className="
        relative
        flex items-stretch gap-4
        rounded-2xl
        border border-slate-200
        bg-white
        px-4 py-3
        shadow-sm
        hover:border-[#00C3A0]
        hover:shadow-md
        transition-all
        overflow-hidden
        min-h-[115px]
      "
    >
      {/* Provider */}
      <div className="flex items-center pr-2">
        <div className="text-2xl font-extrabold text-slate-900 whitespace-nowrap">
          {provider}
        </div>
      </div>

      {/* Middle column */}
      <div className="flex-1 flex flex-col justify-between min-w-0">
        <div>
          <div className="text-[11px] uppercase tracking-wide text-slate-400 font-medium">
            TRK#
          </div>
          <div className="text-sm font-semibold text-slate-900 break-all">
            {trackingNumber}
          </div>
          <div className="text-xs text-slate-500 truncate max-w-full">
            {companyName}
          </div>
        </div>

        <div>
          <div className="flex items-center gap-2">
            <span className="text-sm font-semibold text-[#00997F] whitespace-nowrap">
              {formattedPrice}
            </span>

            {invoiceCount && invoiceCount > 1 && (
              <span className="inline-flex items-center rounded-full bg-slate-100 px-2 py-0.5 text-[10px] font-medium text-slate-600 whitespace-nowrap">
                {t("dashboard.invoiceCount")}: {invoiceCount}
              </span>
            )}
          </div>

          <div className="mt-1 text-[11px] text-slate-500 flex items-center gap-1">
            <span className="inline-flex items-center justify-center rounded-full bg-slate-100 px-2 py-0.5 whitespace-nowrap">
              {originCountry}
            </span>
            <span className="text-slate-400">→</span>
            <span className="inline-flex items-center justify-center rounded-full bg-slate-100 px-2 py-0.5 whitespace-nowrap">
              {destinationCountry}
            </span>
          </div>
        </div>
      </div>

      {/* Right column: date + badge + history icon */}
      <div className="flex flex-col justify-between items-end text-right whitespace-nowrap">
        <div className="text-xs text-slate-500">{formattedDate}</div>

        <div className="flex items-center gap-1 mt-2">
          {/* Mode pill */}
          <span
            className={`
              inline-flex items-center rounded-full px-3 py-1 text-[11px] font-semibold
              ${
                isExport
                  ? "bg-[#D9F7EF] text-[#00997F]"
                  : "bg-[#1F2937] text-white"
              }
            `}
          >
            {modeLabel}
          </span>

          {/* History icon */}
          {hasHistory && (
            <button
              type="button"
              onClick={onShowHistory}
              className="
                p-1
                hover:bg-slate-100
                rounded-full
                transition
              "
              title={t("dashboard.showHistory")}
            >
              <Clock
                size={16}
                className="text-slate-500 hover:text-[#00997F]"
              />
            </button>
          )}
        </div>
      </div>
    </div>
  );
}
