"use client";

import { useState, useRef } from "react";
import type { ChangeEvent, DragEvent } from "react";

import { useI18n } from "@/i18n/i18nProvider";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
  DialogTrigger,
} from "@/shared/ui/dialog";
import { Button } from "@/shared/ui/button";
import { Badge } from "@/shared/ui/badge";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/shared/ui/table";
import { ChevronRight, Trash2, Plus } from "lucide-react";

import { useInvoiceUpload } from "@/hooks/useInvoiceUpload";
import { uploadInvoices } from "@/app/api/invoices";
import type { InvoiceRecordJson } from "@/lib/types/invoice.types";

type UploadInvoicesDialogProps = {
  onUploaded?: () => void;
};

export function UploadInvoicesDialog({
  onUploaded,
}: UploadInvoicesDialogProps) {
  const { t } = useI18n();

  const [open, setOpen] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [isDragging, setIsDragging] = useState(false);

  const fileInputRef = useRef<HTMLInputElement | null>(null);

  const {
    parsedFiles,
    isParsing,
    addFiles,
    toggleFileExpanded,
    removeFile,
    reset,
  } = useInvoiceUpload();

  const resetState = () => {
    reset();
    setError(null);
    setIsUploading(false);
    setIsDragging(false);
  };

  const handleOpenChange = (nextOpen: boolean) => {
    setOpen(nextOpen);
    if (!nextOpen) resetState();
  };

  // kliknutí / výběr souborů
  const handleFileInputChange = async (e: ChangeEvent<HTMLInputElement>) => {
    const fileList = e.target.files;
    if (!fileList || fileList.length === 0) return;

    const result = await addFiles(Array.from(fileList));

    if (result === "invalid-json") {
      setError(t("uploadDialog.invalidJson"));
    }

    e.target.value = "";
  };

  // drag & drop do čtverce
  const handleDragOver = (e: DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.dataTransfer.dropEffect = "copy";
    setIsDragging(true);
  };

  const handleDragLeave = (e: DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    setIsDragging(false);
  };

  const handleDrop = async (e: DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    setIsDragging(false);

    const jsonFiles = Array.from(e.dataTransfer.files).filter(
      (file) =>
        file.type === "application/json" ||
        file.name.toLowerCase().endsWith(".json")
    );
    if (jsonFiles.length === 0) return;

    const result = await addFiles(jsonFiles);

    if (result === "invalid-json") {
      setError(t("uploadDialog.invalidJson"));
    }
  };

  const handleConfirmUpload = async () => {
    if (parsedFiles.length === 0) {
      setError(t("uploadDialog.invalidJson"));
      return;
    }

    const allRecords: InvoiceRecordJson[] = parsedFiles.flatMap(
      (pf) => pf.records
    );

    setIsUploading(true);
    setError(null);

    try {
      await uploadInvoices(allRecords);
      setOpen(false);
      resetState();
      onUploaded?.();
    } catch (err) {
      console.error(err);
      setError(t("uploadDialog.errorUpload"));
    } finally {
      setIsUploading(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={handleOpenChange}>
      <DialogTrigger asChild>
        <Button
          className="rounded-full bg-[#00C3A0] hover:bg-[#009E85] text-white"
          size="sm"
          type="button"
        >
          {t("dashboard.uploadInvoices")}
        </Button>
      </DialogTrigger>

      {/* větší dialog */}
      <DialogContent className="max-w-5xl">
        <DialogHeader>
          <DialogTitle>{t("uploadDialog.title")}</DialogTitle>
          <DialogDescription>{t("uploadDialog.helperText")}</DialogDescription>
        </DialogHeader>

        <div className="space-y-4">
          {/* JEDEN jemný čtverec = drop zóna + obsah (placeholder nebo soubory) */}
          <div
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
            onDrop={handleDrop}
            className={`border border-dashed rounded-2xl bg-slate-50/50 transition px-4 ${
              parsedFiles.length === 0 ? "py-8" : "py-4"
            } ${
              isDragging
                ? "border-[#00C3A0] bg-emerald-50/50"
                : "border-slate-200"
            }`}
          >
            {/* skrytý input */}
            <input
              ref={fileInputRef}
              type="file"
              accept="application/json"
              multiple
              className="hidden"
              onChange={handleFileInputChange}
            />

            {parsedFiles.length === 0 ? (
              // placeholder text, když nejsou soubory
              <div
                role="button"
                tabIndex={0}
                onClick={() => fileInputRef.current?.click()}
                onKeyDown={(e) => {
                  if (e.key === "Enter" || e.key === " ") {
                    e.preventDefault();
                    fileInputRef.current?.click();
                  }
                }}
                className="w-full flex flex-col items-center gap-1 text-center cursor-pointer"
              >
                <span className="text-slate-600">
                  {t("uploadDialog.dropHereText")}
                </span>
                <span className="text-xs text-slate-400">
                  {t("uploadDialog.dropHereSubtextPrefix")}{" "}
                  <span className="underline text-[#00997F] hover:text-[#007a64]">
                    {t("uploadDialog.dropHereSubtextClick")}
                  </span>
                </span>
              </div>
            ) : (
              // seznam souborů + rozbalovací náhled, vše uvnitř čtverce
              <div className="flex flex-col gap-3">
                <div className="flex items-center justify-between">
                  <span className="text-xs text-slate-500">
                    {t("uploadDialog.uploadedFilesLabel")}
                  </span>
                  <Button
                    type="button"
                    variant="ghost"
                    size="sm"
                    className="h-7 px-2 text-xs text-slate-600 hover:bg-slate-100"
                    onClick={() => fileInputRef.current?.click()}
                  >
                    <Plus className="h-3 w-3 mr-1" />
                    {t("uploadDialog.addMore")}
                  </Button>
                </div>

                {parsedFiles.map((pf) => (
                  <div
                    key={pf.id}
                    className="border border-slate-200 rounded-xl bg-white"
                  >
                    <div className="flex items-center justify-between px-4 py-2">
                      <button
                        type="button"
                        onClick={() => toggleFileExpanded(pf.id)}
                        className="flex items-center gap-2 text-left"
                      >
                        <ChevronRight
                          className={`h-4 w-4 transition-transform ${
                            pf.expanded ? "rotate-90" : ""
                          }`}
                        />
                        <div className="flex flex-col">
                          <span className="text-sm font-medium truncate max-w-xs sm:max-w-sm">
                            {pf.file.name}
                          </span>
                          <span className="text-xs text-slate-400">
                            {pf.rows.length} {t("uploadDialog.rowsLabel")}
                          </span>
                        </div>
                      </button>

                      <button
                        type="button"
                        onClick={() => removeFile(pf.id)}
                        className="inline-flex items-center justify-center rounded-full p-1.5 text-slate-400 hover:text-red-600 hover:bg-red-50 transition"
                        aria-label={t("uploadDialog.removeFile")}
                      >
                        <Trash2 className="w-4 h-4" />
                      </button>
                    </div>

                    {pf.expanded && pf.rows.length > 0 && (
                      <div className="border-t border-slate-100 max-h-56 overflow-y-auto">
                        <Table className="w-full text-sm table-fixed">
                          <TableHeader>
                            <TableRow>
                              <TableHead className="w-[140px]">
                                {t("uploadDialog.colTracking")}
                              </TableHead>
                              <TableHead className="w-[220px]">
                                {t("dashboard.company")}
                              </TableHead>
                              <TableHead className="w-[100px]">
                                {t("uploadDialog.colProvider")}
                              </TableHead>
                              <TableHead className="w-[120px]">
                                {t("uploadDialog.colRoute")}
                              </TableHead>
                              <TableHead className="text-right w-[90px]">
                                {t("uploadDialog.colPrice")}
                              </TableHead>
                              <TableHead className="text-right w-[90px]">
                                {t("uploadDialog.colWeight")}
                              </TableHead>
                            </TableRow>
                          </TableHeader>
                          <TableBody>
                            {pf.rows.map((row) => (
                              <TableRow key={row.id}>
                                <TableCell>{row.trackingNumber}</TableCell>
                                <TableCell className="truncate">
                                  {row.companyName}
                                </TableCell>
                                <TableCell>{row.provider}</TableCell>
                                <TableCell>
                                  {row.originCountry} → {row.destinationCountry}
                                </TableCell>
                                <TableCell className="text-right text-[#00997F] font-semibold">
                                  {row.invoicedPrice.toFixed(2)}
                                </TableCell>
                                <TableCell className="text-right">
                                  {row.invoicedWeight ?? "-"}
                                </TableCell>
                              </TableRow>
                            ))}
                          </TableBody>
                        </Table>
                      </div>
                    )}
                  </div>
                ))}
              </div>
            )}
          </div>

          {isParsing && (
            <Badge variant="outline">{t("uploadDialog.parsing")}</Badge>
          )}

          {error && (
            <div className="text-sm text-red-600 bg-red-50 border border-red-200 rounded-md px-3 py-2">
              {error}
            </div>
          )}
        </div>

        <DialogFooter>
          <div className="flex gap-2">
            <Button
              variant="outline"
              type="button"
              onClick={() => setOpen(false)}
            >
              {t("uploadDialog.cancel")}
            </Button>
            <Button
              type="button"
              onClick={handleConfirmUpload}
              disabled={isUploading || parsedFiles.length === 0}
              className="bg-[#00C3A0] hover:bg-[#009E85] text-white"
            >
              {isUploading
                ? t("uploadDialog.uploading")
                : t("uploadDialog.confirm")}
            </Button>
          </div>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
